.main-nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 79px;
  height: 78px;
  background: #ffffff;
  cursor: pointer;
  border: none;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 32px;
  left: 28px;
  width: 20px;
  height: 2px;
  background-color: #231f20;
  box-shadow: 0 6px 0 0 #231f20, 0 12px 0 0 #231f20;
}
.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(35, 31, 32, 0.3);
  box-shadow: 0 8px 0 0 rgba(35, 31, 32, 0.3), 0 16px 0 0 rgba(35, 31, 32, 0.3);
}
.main-nav--closed .user__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .user__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}
.main-nav--closed .site__list {
  display: none;
}
@media (min-width: 768px) {
  .main-nav--closed .site__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    z-index: 1;
  }
}
.main-nav--opened .main-nav__toggle {
  top: 0;
  right: 0;
  left: auto;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 37px;
  left: 26px;
  width: 25px;
  height: 2px;
  background-color: #231f20;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  background-color: rgba(35, 31, 32, 0.3);
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
